const Detailed = () => {
  const location = useLocation();
  const [com_dataList, com_setDataList] = useState([]); // 공통 정보 api
  const [intro_dataList, intro_setDataList] = useState([]); // 소개 정보 api
  const { replaceImg, contenttypeid, contentid } = location.state;

  useEffect(() => {
    //공통정보
    const com_getData = async () => {
        await axios({
          method: 'get',
          url: `${baseUrl}/detailCommon1?ServiceKey=${serviceKey}&contentTypeId=${contenttypeid}&contentId=${contentid}&MobileOS=ETC&MobileApp=AppTest&defaultYN=Y&firstImageYN=Y&areacodeYN=Y&catcodeYN=Y&addrinfoYN=Y&mapinfoYN=Y&overviewYN=Y&_type=json`
        }).then((res) => {
          com_setDataList(res.data.response.body.items.item[0]);
        }) 
      }
      com_getData();
    //상세 페이지 아래
      const intro_getDate = async () => {
        await axios({
          method: 'get',
          url:`${baseUrl}/detailIntro1?ServiceKey=${serviceKey}&contentTypeId=${contenttypeid}&contentId=${contentid}&MobileOS=ETC&MobileApp=AppTest&_type=json`
        }).then((res) => {
          // console.log(res)
          intro_setDataList(res.data.response.body.items.item[0]);
        })
      }
      intro_getDate();
  }, []);
  // console.log(com_dataList)
  const title = com_dataList?.title || "해당정보 없음";
  const zipCode = com_dataList?.zipcode || "해당정보 없음";
  const homePage = com_dataList?.homepage || "해당정보 없음";
  const address = com_dataList?.addr1 || "해당정보 없음";
  const firstImage2 = com_dataList?.firstimage2 || "해당정보 없음";
  // const overView = com_dataList?.overview || "";
  const formattedOverview = com_dataList?.overview?.replace(/<br\s*\/?>/g, '\n') || "해당정보 없음";
  // console.log(homePage)
  const extractedUrl = homePage.match(/href="([^"]*)/)?.[1] || "해당정보 없음";
  // console.log(extractedUrl)
  const truncatedOverview = formattedOverview.split('.').slice(0, 2).join('.') || "해당정보 없음";

  // url:`http://apis.data.go.kr/B551011/KorService1/detailIntro1?ServiceKey=eRLOwH19Z%2FYOTC00zcefvpxvwseGUrgSsocmGQ0SiDLenLDTZtVZRYaMYwScpSNCHiQBLDrbYbFFDkyqu%2FdjcQ%3D%3D&contentTypeId=${contenttypeid}&contentId=${contentid}&MobileOS=ETC&MobileApp=AppTest&_type=json`

    // console.log(intro_dataList);
    const infoCenter = intro_dataList?.infocenterleports || intro_dataList?.infocenter|| "해당정보 없음";
    const resDate = intro_dataList?.restdateleports || intro_dataList?.restdate|| "해당정보 없음";
    const expGuide = intro_dataList?.expguide || "해당정보 없음";
    // const useTime = intro_dataList?.usetime || "";
    const parking = intro_dataList?.parkingleports || intro_dataList?.parking ||"해당정보 없음";
    const chkBabyCarriage = intro_dataList?.chkbabycarriageleports ||intro_dataList?.chkbabycarriage || "해당정보 없음";
    const chkPet = intro_dataList?.chkpetleports ||intro_dataList?.chkpet|| "해당정보 없음";
    const chkCreditCard = intro_dataList?.chkcreditcardleports||intro_dataList?.chkcreditcard || "해당정보 없음";
    // console.log(useTime)
    const formattedTime = intro_dataList?.usetimeleports?.replace(/<br\s*\/?>/g, '\n') || "해당정보 없음";
    const formattedTimes = formattedTime.replace(/\(/g, '\n(')
  .replace(/\)/g, ')\n')
  .replace(/\{/g, '\n{')
  .replace(/\}/g, '}\n') || "해당정보 없음";
  // console.log(formattedTimes)

  useEffect(() => {
    // 카카오톡 sdk 추가
    const script = document.createElement("script");
    script.src = "https://developers.kakao.com/sdk/js/kakao.js";
    script.async = true;
    document.body.appendChild(script);
    return () => document.body.removeChild(script);
  }, []);
  
  const shareToKatalk2 = () => {
    // kakao sdk script 부른 후 window.Kakao로 접근
    if (window.Kakao) {
      const kakao = window.Kakao;
  
      // 중복 initialization 방지
      // 카카오에서 제공하는 javascript key를 이용하여 initialize
      if (!kakao.isInitialized()) {
        kakao.init('a27b63b038bcacb06f7c89bd83814c21');
      }
      
      kakao.Link.sendDefault({
        objectType:
              "feed",
        content: {
          title: 
              "MBTI 알아보러 가실래요?",
          description: 
              "개인 MBTI를 조사한 후, 그에맞는 여행지를 추천해드려요 ~!",
          imageUrl: 
              "http://k.kakaocdn.net/dn/d5Yeg6/btsl4nf8NsB/2u8CPaRGvgWx4rIkFdeDk0/kakaolink40_original.png",
            link: {
              mobileWebUrl:
              // "http://localhost:3000/popup",
              // "http://10.210.130.165:3000/",
              `${process.env.PUBLIC_URL}/popup`,
              webUrl:
              // "http://localhost:3000/popup",
              // "http://10.210.130.165:3000/",
              `${process.env.PUBLIC_URL}/popup`,
            },
        },
      });
    }
  };

  const ButtonClick = () => {
    shareToKatalk2();
  };

  return (
    <MainContainer>
      <Header back />
      <ContentContainer style={{marginBottom:"5.875rem"}}>
      <PlaceImage src={contentid === "2638438"|| contentid === "2470006"||  contentid === "2616185"||contentid === "128019" ?  replaceImg : firstImage2} alt="Plaece Image" />

      <Place> {title} </Place>

      <TopContainer>
      <TopDiv>
        우편번호
        <SpacedSpan />
        <TopText>{zipCode}</TopText>
      </TopDiv>

      <TopDiv>
        홈페이지
        <SpacedSpan />
        <TopText>{extractedUrl}</TopText>
      </TopDiv>

      <TopDiv>
        주소
        <SpacedSpan />
        <TopText>{address}</TopText>
      </TopDiv>
      </TopContainer>

      {/* <BtnContainer>
        <ShareButton>공유하기</ShareButton>
      </BtnContainer> */}

      <Button type={"basic"} height={"58px"} onClick={ButtonClick} style={{ margin: "2em 0 1rem 0" }} ><Typography type="bold">링크복사</Typography><Typography type="light">로 여행지 공유하기</Typography></Button>

      <Introduce>소개</Introduce>

      <Description>{truncatedOverview}</Description>

      {/* <BottomDiv>고객센터</BottomDiv>
      <BottomText>{infoCenter}</BottomText>

      <BottomDiv>쉬는 날</BottomDiv>
      <BottomText>{resDate}</BottomText>

      <BottomDiv>체험안내</BottomDiv>
      <BottomText>{expGuide}</BottomText>

      <BottomDiv>이용시간</BottomDiv>
      <BottomText>{formattedTimes}</BottomText>

      <BottomDiv>주차시설</BottomDiv>
      <BottomText>{parking}</BottomText>

      <BottomDiv>유모차 대여 여부</BottomDiv>
      <BottomText>{chkBabyCarriage}</BottomText>

      <BottomDiv>애완동물 동반가능 여부</BottomDiv>
      <BottomText>{chkPet}</BottomText>

      <BottomDiv>신용카드 가능 여부</BottomDiv>
      <BottomText>{chkCreditCard}</BottomText> */}
      </ContentContainer>
    </MainContainer>
  );
};

